import React, { useState } from "react";
import "./App.css";
import { FaRegThumbsUp, FaRegCommentAlt, FaShare } from "react-icons/fa";
function FacebookPost({ post, profile, time, name, text }) {
  const [item, setItem] = useState("");
  const [adding, setAdding] = useState([]);
  const [like, setLike] = useState(false);
  const [input, setInput] = useState(false);

  const handleChange = (e) => {
    setItem(e.target.value);
  };

  const addComments = (e) => {
    e.preventDefault();
    console.log("poka");
    if (item !== "") {
      setAdding((old) => {
        return [...old, item];
      });
    }
    setItem("");
  };

  return (
    <div className="show">
      <div className="show_header">
        <div className="show_header_img">
          <img src={profile} alt="user" />
        </div>
        <div className="show_header_name">
          {name} <div className="date">{time}</div>
        </div>
      </div>

      <div className="show_body">
        <div className="show_body_text">{text}</div>
        <div className="show_body_img">
          <img src={post} alt="post" />
        </div>
      </div>
      <div className="emoji">üëçüòãüòÄüòÅüòéüòçüò•</div>

      <div className="show_reactions">
        <span className="reactions" onClick={() => setLike(!like)}>
          <FaRegThumbsUp className={like ? "dark" : "lite"} />
          <span className="reactions-text" className={like ? "dark" : "lite"}>
            Like
          </span>
        </span>
        <span className="reactions" onClick={() => setInput(!input)}>
          <FaRegCommentAlt className="icons" />
          <span className="reactions-text">Comment</span>
        </span>
        <span className="reactions">
          <FaShare className="icons" />
          <span className="reactions-text">Share</span>
        </span>
      </div>
      <form onSubmit={addComments}>
        {input ? (
          <div className="create">
            <div className="create-first">
              <div className="create-first-img">
                <span>
                  <img src={profile} alt="user" />
                </span>
              </div>

              <div className="create-input">
                <input
                  type="text"
                  className="create-first-input"
                  placeholder="Write a comment..."
                  value={item}
                  onChange={handleChange}
                />
                <ol className="ul">
                  {adding.map((add, index) => {
                    return (
                      <li
                        key={index}
                        className="list"
                        style={{
                          color: "#e4e6eb",
                          padding: "2px",
                        }}
                      >
                        {add}
                      </li>
                    );
                  })}
                </ol>
              </div>
            </div>
          </div>
        ) : (
          ""
        )}
      </form>
    </div>
  );
}

export default FacebookPost;
